import React from 'react';
import Grid from 'material-ui/Grid';

import Info from '../_common/Info';
import Steps from './Steps';
import Href from '../_common/Href';
import {STYLES} from '../../styles/muiTheme';
import {LINKS} from '../../utils/urls';

const responsibilities = [
    {id: 1, text: <span> <strong>  </strong>   ".":</span>,
        subItems: [
            {id: 1, text: '     Service Desk'},
            {id: 2, text: ' '},
            {id: 3, text: '      '},
            {id: 4, text: '   '},
            {id: 5, text: '  '}
        ]
    },
    {id: 2, text: <span> <strong></strong>   <Href href={LINKS.experience.everpoint}>"Everpoint"</Href>:</span>,
        subItems: [
            {id: 1, text: '    '},
            {id: 2, text: '   '},
            {id: 3, text: '  '},
            {id: 4, text: ' '}
        ]
    },
    {id: 3, text: <span> <strong>frontend-</strong>   "Everpoint":</span>,
        subItems: [
            {id: 1, text: '  -   '},
            {id: 2, text: '  REST API'},
            {id: 3, text: '  '},
            {id: 4, text: '    '},
            {id: 5, text: '   '}
        ]
    },
    {id: 4, text: <span>  <strong>frontend-</strong>   <Href href={LINKS.experience.sevenGlyphs}>"7 glyphs"</Href>:</span>,
        subItems: [
            {id: 1, text: '  -   '},
            {id: 2, text: '  REST API'},
            {id: 3, text: '  '},
            {id: 4, text: ' '},
            {id: 5, text: '    '},
            {id: 6, text: '   '},
            {id: 7, text: '     '},
        ]
    }
];

export default function Experience() {
    return (
        <div className="experience page">
            <Grid container spacing={0} style={STYLES.mainGridContainer}>
                <Grid item xs={12} md={6}>
                    <Info
                        title=""
                        subtitle='   "7 glyphs".   -  '
                        color="violet"
                        items={responsibilities}
                    />
                </Grid>
                <Grid item xs={12} md={6}>
                    <Steps />
                </Grid>
            </Grid>
        </div>
    );
}



